import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {updateimg, hideModal, updatedetails} from '../actions';
import counter from '../reducers/counter';

export default function Home()
{
    const counter = useSelector(state => state);  
    const showHide = useSelector(state => state.show);  
    const dispatch = useDispatch();
    return(
        <div className="flex-container">
            {counter.items.map((imgs, i)=><Imgboxes key={i} Imgdetails={imgs} updateProp={dispatch} />)}
            <Modal show={showHide} handleClose={dispatch}></Modal>
        </div>
    )
}

function Imgboxes(props){
    const imagpath = require('../images/'+props.Imgdetails.name); // with require
    return(
        <div className="imageBox">
            <img className="myImg" src={imagpath} alt={props.Imgdetails.title} 
            onClick={()=>props.updateProp(updateimg(props.Imgdetails.id))}/>
            <h3>{props.Imgdetails.title}</h3>
            <div>{props.Imgdetails.author}</div>
            <div>{props.Imgdetails.description}</div>
        </div>
        
    )
}

const Modal = props =>{
    const selectedImg = useSelector(state => state.test);  
    const items = useSelector(state => state.items);  
    const showHideClassName = props.show ? "modal display-block" : "modal display-none";
    //console.log(showHideClassName);    
    const titleRef = React.useRef();
    const idRef = React.useRef();
    const authorRef = React.useRef();
    const descRef = React.useRef();

    const handleSubmit=()=>{
        props.handleClose(updatedetails(idRef.current.value,titleRef.current.value,authorRef.current.value,descRef.current.value));
    }
    
    return(
        <div className={showHideClassName}>
            <section className="modal-main">
                <button className="exitbtn" onClick={()=>props.handleClose(hideModal())}>.</button>
                <div>
                    {items.filter(images=>images.id == selectedImg).map(sletimg=>(
                        <div key={sletimg.title}>
                            <form className="updateForm" onSubmit={e => { e.preventDefault();handleSubmit();
                            }}>
                            <img className="inImg" src={require('../images/'+sletimg.name)} />
                                <label>Title</label><input type="text" name="title" defaultValue={sletimg.title} 
                                ref={titleRef}/>
                                <label>Author</label><input type="text" name="author" defaultValue={sletimg.author} ref={authorRef} />
                                <label>Description</label><input type="text" name="description" defaultValue={sletimg.description} ref={descRef} />
                                <input type="hidden" name="id" value={sletimg.id} ref={idRef}/>
                                <input type="submit" value="Update" />
                            </form>
                        </div>
                        
                    ))}
                </div>

            </section>
        </div>
    )
}