import React from 'react';
import { connect } from 'react-redux';
import { useSelector, useDispatch } from 'react-redux';
import {Authendicate} from '../actions';
import counter from '../reducers/counter';

export default function Login(){
    const isLogged = useSelector(state => state.isLogged);
    const errorMessage = useSelector(state => state.errorM);
    const LoggedUser = useSelector(state => state.users.username);
    const dispatch = useDispatch();
    const usernameRef = React.useRef();
    const passwordRef = React.useRef();
    //const mycss = {display:"none"};
    var mycss = {display:"block"};
    if(errorMessage == ""){
        var mycss = {display:"none"};
    }

    const loginSubmit=()=>{
        dispatch(Authendicate(usernameRef.current.value, passwordRef.current.value));
    }

    if(isLogged == false){
        return(
            <div className="Login">
                <form name="LoginForm" className="LoginForm" onSubmit={e=>{e.preventDefault();loginSubmit();}}>
                    <div style={mycss} className="errorMessage">{errorMessage}</div>
                    <h3>Login</h3>
                    <input type="text" defaultValue="" placeholder="Username" ref={usernameRef} required/>
                    <input type="password" defaultValue="" placeholder="Password" ref={passwordRef} required/>
                    <input type="submit" value="Login" />
                </form>
            </div>
        )
    } else {
        return <div className="gHead"><h2>Welcome { LoggedUser }</h2></div>
    }
   
}