
//REDUCER
const counter = (state={"items": [
    {
        "id":1,
        "title": "Img1",
        "author": "Author 1",
        "description":"New DESC",
        "name":"1.jpg"
    },
    {
        "id":2,
        "title": "Img2",
        "author": "Author 2",
        "description":"New DESC",
        "name":"2.jpg"
    },
    {
        "id":3,
        "title": "Img3",
        "author": "Author 3",
        "description":"New DESC",
        "name":"3.jpg"
    },
    {
        "id":4,
        "title": "Img4",
        "author": "Author 4",
        "description":"New DESC",
        "name":"4.jpg"
    },
    {
        "id":5,
        "title": "Img5",
        "author": "Author 5",
        "description":"New DESC",
        "name":"5.jpg"
    },
    {
        "id":6,
        "title": "Img6",
        "author": "Author 6",
        "description":"New DESC",
        "name":"6.jpg"
    },
    {
        "id":7,
        "title": "Img7",
        "author": "Author 7",
        "description":"New DESC",
        "name":"7.jpg"
    },
    {
        "id":8,
        "title": "Img8",
        "author": "Author 8",
        "description":"New DESC",
        "name":"8.jpg"
    },
    {
        "id":9,
        "title": "Img9",
        "author": "Author 9",
        "description":"New DESC",
        "name":"9.jpg"
    }
],
show: false,
isLogged:false,
users:{
username:"Mohamed",
password:"Mohamed"
},
errorM:""
}, action) => {
    switch (action.type) {
        case 'UPDATE':
            state.show= true;
            state.test=action.id;
            return state;

        case 'CLOSE':
            state.show=false;
            return state;

        case 'UDETAILS':
            if(state.isLogged == false){
                alert('Login to Edit Images');
                break;
            }

            var update = require('immutability-helper');
            var data = state.items;
            var commentIndex = data.findIndex(function(c) { 
                return c.id == action.id; 
            });
        
            var updatedComment = update(data[commentIndex], {title: {$set: action.title}}); 
        
            var newData = update(data, {
                $splice: [[commentIndex, 1, updatedComment]]
            });
            
            state.items=newData;

            var updatedComment2 = update(newData[commentIndex], {author: {$set: action.author}}); 
        
            var newData2 = update(newData, {
                $splice: [[commentIndex, 1, updatedComment2]]
            });
            
            state.items=newData2;

            var updatedComment3 = update(newData2[commentIndex], {description: {$set: action.description}}); 
        
            var newData3 = update(newData2, {
                $splice: [[commentIndex, 1, updatedComment3]]
            });
            
            state.items=newData3;
            state.show=false;
            return state;

        case 'AUTHENDICATE':
            if(state.users.username == action.uname && state.users.password == action.pword){
                state.isLogged=true;
                state.errorM="";
            } else {
                state.errorM="Invalid Credentials"
            }
            return state;
            
        default:
            return state;
    }
    return state;
}
export default counter;
