export const updateimg = (id) => {
    return{
        type: 'UPDATE',
        id:id
    }
}

export const hideModal=()=>{
    return{
        type:'CLOSE'
    }
}

export const updatedetails = (id,title,author,desc) =>{
    return{
        type:'UDETAILS',
        id:id,
        title:title,
        author:author, 
        description:desc
    }
}

export const Authendicate = (username, password)=>{
    return{
        type:'AUTHENDICATE',
        uname:username,
        pword:password
    }
}